.container {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: repeat(2, 1fr);
	grid-gap: 10px;
	overflow: hidden;
	padding-right: 22px;

	.image {
		width: 100%;
		height: 200px;
		&:hover {
			cursor: pointer;
			scale: 1.1;
			transition: ease-in 0.1s;
		}
	}
}
